<template>
  <div class="modify">
    <div class="modifyTop">
      <img
        @click="$router.push({ path: '/my' });"
        src="@/assets/phone/AppVisitorHome/back.png"
        alt
      />
      <div>设置密码</div>
      <img @click="Submit" src="@/assets/phone/deng/完成.png" alt />
    </div>
    <div class="modifyBox">
      <div class="modifyBoxTitle">|&nbsp;&nbsp;设置密码</div>
      <div class="modifyItem">
        <div class="modifyName">
          用户名
          <span class="name">{{ userInfo.userName }}</span>
        </div>
        <div class="modifyPassword">
          <Form
            error-message-align="right"
            input-align="right"
            @submit="onSubmit"
            ref="formData"
          >
            <CellGroup inset>
              <van-field
                v-model="oldPassword"
                label="旧密码"
                type="password"
                name="password"
                placeholder="请输入旧密码"
                :rules="[
                  {
                    required: true,
                    message: '旧密码不能为空',
                    trigger: 'onBlur',
                  },
                ]"
              />
              <van-field
                v-model="newPassword"
                label="新密码"
                name="newpassword"
                type="password"
                placeholder="请输入新密码"
                :rules="[
                  {
                    required: true,
                    message: '新密码不能为空',
                    trigger: 'onBlur',
                  },
                  {
                    validator: checkNewPasswordLength,
                    message: '新密码不符合要求',
                    trigger: 'onBlur',
                  },
                ]"
              />
              <van-field
                v-model="newAginPassword"
                label="确认密码"
                name="aginpassword"
                type="password"
                placeholder="请再次输入新密码"
                :rules="[
                  {
                    required: true,
                    message: '确认密码不能为空',
                    trigger: 'onBlur',
                  },
                  {
                    validator: passwordValidator,
                    message: '确认密码与新密码不一致',
                    trigger: 'onBlur',
                  },
                ]"
              />
            </CellGroup>
          </Form>
        </div>
      </div>
      <div class="modifyText">密码须包含字母和数字,长度在8-16之间。</div>
      <!-- <div class="ForgetPassword">忘记旧密码</div> -->
      <div class="SubmitBtn" @click="Submit">提&nbsp;&nbsp;交</div>
    </div>
  </div>
</template>

<script>
import { Encrypt } from "@/api/tyrz/DES";
import { Form, CellGroup } from "vant";
import { updateUserPassword } from "@/api/data/reservation";
import { cryptographicStrength } from "@/utils/validate";
export default {
  components: {
    Form,
    CellGroup,
  },
  data() {
    return {
      userInfo: {},
      oldPassword: "",
      newPassword: "",
      newAginPassword: "",
    };
  },

  methods: {
    passwordValidator(val) {
      if (val == "" || val == null) {
        return false;
      } else if (this.newPassword != this.newAginPassword) {
        return false;
      } else {
        return true;
      }
    },
    checkNewPasswordLength(val) {
      let reg = /^(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]*).{8,16}$/;
      if (val == "" || val == null) {
        return false;
      } else if (!reg.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    onSubmit(info) {
      let _this = this;
      updateUserPassword({
        password: Encrypt(_this.newPassword),
        oldPassword: Encrypt(_this.oldPassword),
      }).then((res) => {
        if (!res.data.code) {
          _this.$message.success('修改成功');
          history.go(-1);
        } else {
          _this.$message.warning(res.data.msg);
        }
      });
    },
    Submit() {
      this.$refs.formData.submit();
    },
  },
  mounted() {},
  created() {
    this.userInfo = this.$deepCopy(this.$route.params.getUserInfoData);
  },
};
</script>
<style lang="scss">
.modifyPassword {
  .van-cell-group--inset {
    margin: unset;
    border-radius: unset;
    .van-cell {
      background-color: #f9f9f9;
      padding: 10px 0;
      &::after {
        border-bottom-color: #979797;
        right: 0;
        left: 0;
      }
    }
  }
  .van-field__control {
    // text-align: right;
    padding-right: 5px;
  }
  .van-hairline--top-bottom::after {
    border-bottom: 1px solid #979797;
    border-top: 0px;
  }
}
</style>
<style lang='scss' scoped>
.modify {
  width: 100vw;
  height: 100vh;
  background-color: #f9f9f9;
  .modifyTop {
    width: 100vw;
    height: 9vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    align-items: center;
    img:nth-child(1) {
      width: 7vw;
      height: 7vw;
      margin-left: 5vw;
    }
    img:nth-child(3) {
      width: 7vw;
      height: 7vw;
      margin-left: 28vw;
    }
    div {
      margin-left: 25vw;
      font-size: 6.5vw;
      color: white;
    }
  }
  .modifyBox {
    width: 95vw;
    margin: 0 auto;
    padding-top: 8vw;
    box-sizing: border-box;
    height: 91vh;
    .modifyBoxTitle {
      padding: 1vw;
      font-weight: bold;
      font-size: 4.7vw;
      background-color: white;
    }
    .modifyItem {
      margin-top: 4vw;
      font-size: 4.8vw;
      color: #a6a6a6;
      padding: 2.5vw;
      border: 0.1vw solid #a6a6a663;
      border-radius: 1vw;
      .modifyName {
        margin-bottom: 3vw;
        .name {
          margin-left: 5vw;
        }
      }
      .modifyPassword {
        width: 100%;
        font-weight: bold;
      }
    }
    .modifyText {
      color: #d6d6d6;
      font-size: 3.8vw;
      font-weight: bold;
      padding: 2.5vw;
    }
    .ForgetPassword {
      color: #5a9aff;
      font-size: 3.8vw;
      font-weight: bold;
      padding: 0 2vw 2vw;
    }
    .SubmitBtn {
      margin-top: 70vw;
      height: 5vh;
      line-height: 5vh;
      text-align: center;
      background-color: #15cfa4;
      font-size: 4.7vw;
      color: white;
    }
  }
}
</style>